___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    display: flex;
    height: 100vh;
  }
  
  .dashboard-container {
    display: flex;
    width: 100%;
  }
  
  .sidebar {
    width: 250px;
    background-color: #2c3e50;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .sidebar-header {
    padding: 20px;
    text-align: center;
    background-color: #34495e;
  }
  
  .nav-menu ul {
    list-style: none;
    padding: 0;
  }
  
  .nav-menu li {
    padding: 15px 20px;
  }
  
  .nav-menu li a {
    text-decoration: none;
    color: white;
    display: block;
    transition: background 0.3s;
  }
  
  .nav-menu li a:hover {
    background-color: #1abc9c;
  }
  
  .main-content {
    flex-grow: 1;
    padding: 20px;
    background-color: #ecf0f1;
  }
  
  .main-header {
    background-color: #bdc3c7;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
  }
  
  .cards {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  }
  
  .card {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    flex: 1;
    text-align: center;
  }
  
  .card h3 {
    margin: 0;
  }
  
  .overview {
    margin-top: 20px;
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
